@import '../../styles/_variables';
#news-listing-page {
    #page-title {
        color: $eastern-blue;
        font-family: $font-optima;
        font-size: 30px;
        margin-top: 35px; }
    .press-release-container {
        margin-bottom: 30px; }
    .no-data {
        font-family: $font-calibri-regular;
        font-size: 40px;
        height: 300px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: $dim-gray; }
    .full-page-loader-container {
        height: 450px;
        display: flex;
        justify-content: center;
        align-items: center; }
    .load-more-loader-container {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 40px 0; }

    .load-more-container {
        width: 100%;
        padding-left: 15px;
        padding-right: 15px;
        #load-more {
            padding: 20px;
            background-color: $dark-gray;
            text-align: center;
            margin-top: 10px;
            color: $dim-gray;
            font-family: $font-optima;
            font-size: 24px;
            display: block;
            width: 100%;
            outline: 0;
            border: 0;
            cursor: pointer;
            margin-bottom: 80px; } } }
